export default {
	data() {
		return {
			details: [],
			stute: ''
		};
	},
	onLoad(option) {
		var that = this;
		console.log('code=' + option.phone);
		uni.request({
			url: 'http://106.13.239.202/index.php?c=rent&m=saledetails',
			method: 'POST',
			header: { 'content-type': 'application/x-www-form-urlencoded' },
			data: {
				phone: option.phone
			},
			success: res => {
				console.log(res.data.data);

				that.details = res.data.data;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {}
};
